@import url("https://fonts.googleapis.com/css2?family=Pacifico:wght@200;400;600&display=swap");
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css");

.button-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.yesButton {
  background-color: indigo; /*button*/
  color: white; /*text*/
  font-family: "Pacifico", cursive;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s;
  align-items: center;
  justify-content: center;
  display: flex;
  margin-right: 5px;
}

.yesButton:hover {
  background-color: #23055a;
  align-items: center;
}

.noButton {
  background-color: gray;
  color: white;
  font-family: "Pacifico", cursive;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s;
  align-items: center;
  justify-content: center;
  display: flex;
  margin-left: 5px;
}

.noButton:hover {
  background-color: #3c3a3f;
  align-items: center;
}
/*background color and animation*/

body {
  background-color: black;
  color: white;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Pacifico", cursive;
  margin: 0;
  min-height: 100vh;
  overflow: auto;
}

.heart {
  position: fixed;
  font-size: 1.5rem;
  top: 0;
  transform: translateY(0);
  animation: fall 3s linear forwards;
  z-index: 9999;
  pointer-events: none;
}

@keyframes fall {
  from {
    transform: translateY(0vh) translateX(-10vw);
  }
  to {
    transform: translateY(110vh) translateX(10vw);
  }
}

.author {
  position: fixed;
  color: gray;
  font-size: 14px;
  bottom: 10px;
  right: 10px;
  margin-left: 30px;
  font-style: italic;
  font-weight: bold;
  font-family: Copperplate, Papyrus, fantasy;
}
